.footer-img {
  max-width: 100%;
  width: 100%;
  height: 260px;
}

@media screen and (max-width: 425px) {
  .footer-img {
    max-width: 100%;
    width: 100%;
    height: 140px;
  }
}
@media screen and (max-width: 375px) {
  .footer-img {
    height: 150px;
  }
}



